const History = () => {
  

  const getTx = () => { 

    axios.get(`https://${process.env.REACT_APP_URL}/api/v1/tx/${localStorage.getItem("address")}`)
    .then((_data)=> {
      console.log(_data)
      setData(_data.data)
      return _data.data
      
    })
  }

 

  const [data,setData] = useState([])



  return (
    <div className="table-container">

      {
        data.length==0 ? 
        
        <Style>
      <h2>TX HISTORY</h2>
      <div className="holder" data-aos="fade-up">
        <Table>
       
         
        
         <h3> Load Transactions</h3>
         <Img onClick={getTx} src="https://img.icons8.com/ios-glyphs/30/ffffff/refresh--v1.png"/>
        </Table>
      </div>
      {/* <PaginationNb pages="7" /> */}
    </Style>
        
        
        
        :

        <Style>
        <h2>TX HISTORY</h2>
        <div className="holder" data-aos="fade-up">
          <Table>
         
            <tr>
              <th>#</th>
              <th>TX Type</th>
              <th>TX Hash</th>
              <th>Quantity</th>
              <th>Status</th>
              <th> <Img onClick={getTx} src="https://img.icons8.com/ios-glyphs/30/ffffff/refresh--v1.png"/></th>
            </tr>
          
            {data.map((item, index) => (
              <tr>
                <td>{index + 1}</td>
                <td>{item.type}</td>
                <td>{item.hash}</td>
                <td>{item.quantity}</td>
                <td>
                  {item.status}
                </td>
              </tr>
            ))}
          </Table>
        </div>
        {/* <PaginationNb pages="7" /> */}
      </Style>

        
      }
          
    </div>

  );
};

export default History;

