import React , { useState, useEffect } from "react";
import styled from 'styled-components';
// import { Link } from "react-router-dom";
import { ReactComponent as Logo } from '../imgs/AngryLogo.svg';
import { ReactComponent as CloseIcon } from '../imgs/close.svg';
import { ReactComponent as BurgerIcon } from '../imgs/burger.svg';

function Navbar() {
    const [scroll, setScroll] = useState(false);
	const [menu, setMenu] = useState(false);
    const [currentLink, setCurrentLink] = useState(0);


    useEffect(() => {
        window.addEventListener("scroll", () => {
          setScroll(window.scrollY > 15);
        });
      }, []);

    return (
            <Nav id="home" className={scroll ? "is-scroll" : "" }>
                <div className="container">
                    <div className="nav">
                        <a href="/" className="logo">
                            <Logo/>
                        </a>
                        <Navs>
                        <div items={ ['about', 'elves', 'roadmap'] }  className={menu ? "navLinks show" : "navLinks" } >
                                    <a href="#about" onClick={()=>{setMenu(false); setCurrentLink(0)}} className={currentLink === 0 ? "active nav" : "nav"}>
                                         <span>About Us</span>
                                        {currentLink === 0 && <div className="" />}
                                    </a>
                                    <a href="#about" onClick={()=>{setMenu(false) ; setCurrentLink(1)}} className={currentLink === 1 ? "active nav" : "nav"} >
                                    <span>Recruit an Elf</span>
                                    {currentLink === 1 && <div className="" />}
                                    </a>
                                    <a href="#roadmap" onClick={()=>{ setMenu(false); setCurrentLink(2)  }} className={currentLink === 2 ? "active nav" : "nav"} >
                                    <span>Roadmap</span>
                                    {currentLink === 2 }
                                        </a>
                        </div>
                            <button onClick={()=>setMenu(!menu)} style={{"zIndex": "11"}} >
                                {menu ? <CloseIcon /> : <BurgerIcon/>}
                            </button>
                        </Navs>
                    </div>
                </div>
            </Nav>
    )
}

const Nav = styled.nav`
    position: fixed;
    width: 100%;
    padding: 41px 0;
    background-color: transparent;
    flex-wrap: nowrap;
    transition: background-color 200ms, top 200ms;
    z-index: 10;
    .nav{
        display: flex;
        align-items: center;
        .logo{
            margin-right: 81px;
            cursor: pointer;
            display: flex;
            align-items: center;
            @media (max-width: 1200px){
                width: 146px;
                height: 17px;
            }
        }
        button{
            display: none;
            @media (max-width: 768px){
                position: relative;
                display: inline-block;
                background-color: unset;
                border: unset;
                cursor: pointer;
                z-index: 20;
            }
        }
        @media (max-width: 768px){
            justify-content: space-between;
            padding-top: 0;
        }
    }
    &.is-scroll{
		padding: 15px 0;
		background-color: #16151C;
		top: 0;
		transition: background-color 200ms, top 200ms;
        .navLinks{
            color: white !important;
        }
	}
    @media (max-width: 768px){
        padding: 20px 0;
    }
`

const Navs = styled.div`
    .navLinks{
        display: flex;
        a{
            &:hover, &.active {
                color: #952AE4;
            }
            &:not(:first-child){
                margin-left: 44px;
                white-space: nowrap;
            }
        }
        &.show{
            left: 0 !important;
            transition: left 200ms;
        }
    }
    @media (max-width: 768px){
        .navLinks{
            text-align: center;
            background-color: #16151C;
            width: 100%;
            height: 100%;
            inset: 0;
            left: -100%;
            transition: left 200ms;
            position: fixed;
            align-items: center;
            justify-content: center;
            flex-direction: column;
            z-index: 10;
            a{
                display: flex;
                justify-content: center;
                &:not(:first-child){
                    margin-left: 0;
                }
                margin-bottom: 20px;
                width: 100%;
                padding: 30px 0;
                &:hover, &active{
                    background-color: #313236;
                    color: #952AE4 !important;
                }
            }
        }
    }
`

export default Navbar